<template>
  <div :class="{'not-side': !isAuth, 'short-nav': !isOpen}">
    <div id="header" class="d-flex align-items-center justify-content-between" v-if="isAuth">
      <div class="v-center">
        <h1 class="mr-3">{{ pageTitle }}</h1>
        <span class="font-weight-bold mr-4">{{ currentUser.full_name || ' ' }}</span>
      </div>
      <div class="v-center">
        <div class="dropdowns">
          <b-dropdown variant="link" size="lg" no-caret right>
            <template slot="button-content">
              <!--<vector icon="user"/>-->
              <img src="/icons/user.svg">
            </template>
            <b-dropdown-item href="#" @click="logout"></b-dropdown-item>
          </b-dropdown>
        </div>
      </div>
    </div>
    <div id="nav" v-if="isAuth">
      <div class="nav-header d-center">
        <!--<span>ASD LOGO</span>-->
      </div>
      <div class="nav-toggle d-center" @click="navToggle">
        <!--<vector icon="long-arrow-left" v-if="isOpen"/>-->
        <!--<vector icon="bars" v-else/>-->
      </div>
      <div class="nav-body">
        <ul id="nav-menu">
          <menu-item :model="item" v-for="(item, i) in navItems" :key="i"/>
        </ul>
      </div>
    </div>
    <div id="content">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
  import {mapActions, mapState} from 'vuex';

  export default {
    data() {
      return {
        isOpen: true,
        pageTitle: '',
        navItems: [],
      }
    },
    watch: {
      '$route'(to, from) {
        this.pageTitle = to.meta.title || '';
      }
    },
    mounted() {
      if (this.isAuth) {
        this.getCurrentUser();
        this.pageTitle = this.$route.meta.title || '';
      }
    },
    computed: {
      ...mapState({
        isAuth: state => state.auth.auth,
        currentUser: state => state.auth.user
      }),
    },
    methods: {
      ...mapActions('auth', ['setUser', 'logout']),
      navToggle() {
        this.isOpen = !this.isOpen
      },
      getCurrentUser() {
        return new Promise((resolve, reject) => {
          axios.get('/credentials')
            .then(response => {
              this.setUser(response.data);
              resolve();
            })
            .catch(error => {
              this.logout();
              reject();
            });
        });
      },
    }
  }
</script>

<style lang="scss">

  #header {
    position: fixed;
    top: 0;
    left: 320px;
    right: 0;
    z-index: 100;
    height: 80px;
    padding: 0 25px;
    background-color: theme-color('success');
    color: white;
    h1 {
      font-size: 28px;
    }
    .icon {
      width: 30px;
      height: 32px;
      color: white;
    }
    .dropdown {
      .btn {
        min-width: auto;
      }
    }

  }

  #nav {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    z-index: 100;
    width: 320px;
    background-color: #2e3642;
    color: white;
    .nav-toggle {
      position: absolute;
      right: 0;
      top: 0;
      width: 80px;
      height: 80px;
      background-color: darken(theme-color('success'), 20%);
      cursor: pointer;
      .icon {
        width: 35px;
        height: 38px;
      }
    }
    .nav-header {
      height: 80px;
      padding-right: 80px;
    }
  }

  #content {
    padding: 100px 20px 20px;
    margin-left: 320px;
  }

  .not-side {
    #content {
      margin-left: 0;
    }
  }

  #nav-menu {
    height: 900px;
    overflow-y: auto;
    &, & ul {
      list-style: none;
      padding-left: 0;
    }
    > li {
      // 2 level
      > div > div > ul {
        margin-top: 10px;
        padding-left: 30px;
        border-left: 1px solid #a1a1a1;
        list-style-type: disc;
        color: #00c690;
        div {
          color: #a1a1a1;
        }
        // 3 level
        ul {
          margin: 3px 0 5px 12px;
          padding-left: 10px;
          border-left: 1px solid #a1a1a1;
        }
      }
    }
  }

  .short-nav {
    #header {
      left: 80px;
    }
    #nav {
      width: 80px;
      .nav-header {
        height: 80px;
        padding-right: 0px;
      }
      .nav-toggle {
        position: static;
      }
    }
    #content {
      margin-left: 80px;
    }

    #nav-menu {
      > li > .menu-item-wrapper {
        &:hover {
          > .menu-item-content {
            display: block;
          }
        }
        > .menu-icon {
          height: 60px;
          width: 100%;
        }
        > .menu-item-content {
          position: absolute;
          left: 80px;
          display: none;
          min-width: 200px;
          padding: 20px;
          background-color: #1e242c;
        }
      }
    }
  }

</style>



